import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import wx from 'weixin-js-sdk';
let AppList = class AppList extends Vue {
    constructor() {
        super(...arguments);
        //5693c8b0-f97f-11ea-8165-3111824eebe8
        this.businessId = '5693c8b0-f97f-11ea-8165-3111824eebe8';
        this.classList = [];
        this.addValue = '';
    }
    mounted() {
        // console.log(this.$refs)
        // let btn = document.getElementById('launch-btn');
        this.$refs.launchBtn.addEventListener('launch', function (e) {
            console.log('success');
        });
        this.$refs.launchBtn.addEventListener('error', function (e) {
            console.log('fail', e);
        });
        this.getWeiXinConfig();
        this.$store.commit('SET_SESSION_ID', 'noSession');
        this.getCommodityClassList();
    }
    getCommodityClassList() {
        const toast = this['$createToast']({
            time: 0,
            txt: '数据获取中...'
        });
        toast.show();
        this.$store.dispatch('getCommodityClassList', { business: this.businessId }).then((response) => {
            if (response.success) {
                let list = response.data.list;
                this.$set(this, 'classList', list);
            }
            toast.hide();
        });
    }
    inputChange(index, item) {
        item.change = true;
        this.$set(this.classList, index, item);
    }
    classAdd() {
        let addObj = {
            businessId: this.businessId,
            id: '',
            level: 1,
            name: '',
            change: true
        };
        this.classList.push(addObj);
    }
    saveClick(index, item) {
        if (!item.name) {
            const toast = this.$createToast({
                txt: '分类名称必填',
                time: 2000,
                type: 'warn'
            });
            toast.show();
            return;
        }
        let queryObj = {
            businessId: item.businessId,
            id: item.id,
            level: item.change,
            name: item.name,
        };
        this.$store.dispatch('addOrUpdateCommodityClass', queryObj).then((response) => {
            if (response.success) {
                item.change = false;
                if (item.id) {
                    this.$set(this.classList, index, item);
                }
                else {
                    this.$set(this.classList, index, response.data);
                }
                const toast = this.$createToast({
                    txt: '保存成功',
                    time: 2000,
                    type: 'warn'
                });
                toast.show();
            }
        });
    }
    showCommodity(item) {
        let queryObj = {
            id: item.id,
            name: item.name
        };
        this.$router.push({ path: '/commodityManage', query: queryObj });
    }
    deleteClass(index) {
        this.classList.splice(index, 1);
    }
    previewCommodity() {
        this.$router.push({ path: '/commodityClass', query: { state: 'business@5693c8b0-f97f-11ea-8165-3111824eebe8' } });
    }
    getWeiXinConfig() {
        // let url = window.location.origin + window.location.pathname
        let url = window.location.href;
        this.$store.dispatch('getWeixinConfig', { url: url }).then((res) => {
            console.log('获取微信配置成功', res);
            if (res.success) {
                let config = res.data;
                wx.config({
                    debug: true,
                    appId: config.appId,
                    timestamp: config.timestamp,
                    nonceStr: config.nonceStr,
                    signature: config.signature,
                    jsApiList: ['wx-open-launch-weapp'],
                    openTagList: ['wx-open-launch-weapp']
                });
            }
            wx.ready(function () {
            });
            wx.error(function (err) {
            });
        });
    }
};
AppList = __decorate([
    Component({
        components: {
            page
        }
    })
], AppList);
export default AppList;
